@import '~assets/styles/h3Imports';

.footer {
  border-top: 1px solid var(--footer-border-top);
}

.banner {
  align-items: center;
  background-color: var(--footer-banner-background);
  color: var(--footer-banner-texts);
  display: grid;
  justify-content: center;
  justify-items: center;
  min-height: var(--footer-banner-min-height, 328px);
  overflow: hidden;
  padding: rem(100) 0;
  position: relative;

  --icon-stroke: var(--footer-banner-texts);
  --button-min-width: 168px;
  --button-min-width-small: 199px;

  .icon {
    span {
      height: rem(40);
      width: rem(40);
    }

    svg {
      height: 100%;
      width: 100%;
    }
  }

  .text-label {
    margin: 0;
    text-align: center;
    z-index: var(--footer-z-index-content, 1);
  }

  .heading {
    display: flex;
    flex-direction: column;
    margin: 24px 0;
    text-align: center;
    z-index: var(--footer-z-index-content, 1);
  }

  span {
    z-index: var(--footer-z-index-content, 1);
  }

  a {
    height: auto;
    margin: 0;
    padding-bottom: 14px;
    padding-top: 14px;
    z-index: var(--footer-z-index-content, 1);
  }

  .svg-left-bottom {
    bottom: 0;
    left: 0;
    position: absolute;
  }

  .svg-right-bottom {
    bottom: 0;
    position: absolute;
    right: 0;
  }

  .svg-left-top {
    left: 0;
    position: absolute;
    top: 0;
  }

  .svg-right-top {
    position: absolute;
    right: 0;
    top: 0;
  }
}

.links-copyright {
  display: flex;
  flex-wrap: wrap;
}

.links-list {
  column-gap: 32px;
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
  justify-content: center;
  list-style-type: none;
  margin: 18px 0;
  padding: 0 12px;
  text-align: center;

  li {
    margin: 0;
  }
}

@media #{$small-down} {
  .links-list {
    flex-direction: column;

    li:not(:first-of-type) {
      margin-top: 16px;
    }
  }

  .banner {
    padding: rem(84) 0;

    .heading {
      max-width: var(--footer-headig-max-width, 19.125rem);
    }

    a {
      font-size: rem(18); //figma shows this font and line-height on mobile
      line-height: line-height(20, 18);
    }

    --second-disapear: none; //second svg background stroke to disapear to be used on display property
  }
}

@media #{$medium-down} {
  margin-top: var(--spacing-l);

  .links-list {
    margin-bottom: 0;
  }

  .links-copyright {
    flex-direction: column;

    .copyright {
      margin: 0 0 18px;
    }
  }

  .banner {
    --first-disapear: none; //first svg background stroke to disapear to be used on display property
  }
}

.copyright {
  align-items: center;
  color: var(--footer-copyright-text);
  display: flex;
  flex-grow: 1;
  justify-content: center;
  margin: 18px 0;
  padding: 0 12px;
  text-align: center;
}
